@keyframes spring {
  0%   { opacity: .25; transform: translateY(5px); }
  100% { opacity: 1; transform: translateY(-5px);}
}

.chat-messages {

    &__typing-indicator {
        bottom: 0;
        color: #000;
    }

    &__elipsis {
        margin-left: .25em;
        position: relative;
        & > span {
            animation: spring;
            animation-direction: alternate;
            animation-duration: 1s;
            animation-iteration-count: infinite;
            background-color: #000;
            border-radius: 50%;
            display: inline-block;
            height: 6px;
            width: 6px;

            &:nth-child(2) {
                animation-delay: 100ms;
            }

            &:nth-child(3) {
                animation-delay: 300ms;
            }

            & + span {
                margin-left: .125em;
            }
        }
    }

    &__log {
        flex: auto;
        list-style: none;
        margin: 0;
        overflow-y: scroll;
        padding: 1em 1em 1.5em;
        position: relative;
    }

    &__bubble {
        border-radius: 1em;
        background-color: #e0e0e0;
        display: inline-block;
        max-width: 80%;
        padding: .5em 1em;
        // position: relative;

        &:before {
            border-color: transparent #e0e0e0 transparent transparent;
            border-style: solid;
            border-width: 10px 10px 10px 0;
            content: '';
            height: 0;
            left: -9px;
            position: absolute;
            top: 10px;
            width: 0;
        }
        [data-incoming="false"] & {
            background-color: $color-primary;
            color: #fff;
            left: 20%;

            &:before {
                border-color: transparent transparent transparent $color-primary;
                border-width: 10px 0 10px 10px;
                left: initial;
                right: -9px;
            }
        }
    }

    &__message {
        border: 1rem;
        color: $color-secondary;
        position: relative;

        time {
            color: #2b2b2b;
            display: block;
            font-size: .75em;
            padding: 0 .3rem;
        }

        &[data-incoming="false"] {
            text-align: right;
        }

        & + .chat-messages__message {
            margin-top: .5em;
        }
    }
}
