.chat-header {
    background-color: $color-secondary;
    box-shadow: 0 0 4px #999;
    color: #fff;
    flex-shrink: 0;
    position: relative;
    text-align: center;
    z-index: 1;

    &__caption {
        font: normal 1.25em/2 Verdana, Arial, sans-serif;
        margin: 0;
        padding: .25em 1em;
    }
}
